* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.alpha-wrapper {
  width: 100%;
}

header {
  position: absolute;
  top: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  gap: 2rem;
  padding-left: 2rem;
  font-family: arial;
  color: whitesmoke;
}

ul {
  display: flex;
  gap: 1rem;
  list-style: none;
}

.full-div {
  width: 100%;
  height: 100%;
}

.top-div {
  height: 65%;
  width: 100%;
  display: flex;
}

.left-div {
  display: flex;
  width: 65%;
}

.right-div {
  padding: 5rem;
  width: 35%;
}

.bottom-div {
  height: 35%;
  width: 100%;
  display: flex;
}